<template>
    <!-- banner -->
    <div class="banner">
        <div class="banner-img">
            <ul class="carousel">
            <li class="selection"></li>
            <li></li>
        </ul>
        <ul class="circle">
            <li class="current"></li>
            <li></li>
        </ul>
        </div>
    </div>
</template>
<script>
export default {
    mounted() {
         var currentIndex = 0;//0
        var id;
        var len = document.querySelector('.carousel').childElementCount;//ulli
        function carousel(index) {//
            if (currentIndex === len){ //li0
                currentIndex = index = 0
            }
            if (currentIndex < 0) {
                currentIndex = index = 2
            }
            document.querySelector('.current').className = '';//
            document.querySelector('.circle li:nth-child(' + (index + 1) + ')').className = 'current'//
            document.querySelector('.selection').className = '';//
            document.querySelector('.carousel li:nth-child(' + (index + 1) + ')').className = 'selection'//
        }

        function next() {//
            currentIndex++;//1
            carousel(currentIndex) //carousel
        }
        var lis = document.querySelectorAll('.circle li');//
        for (var i = 0; i < lis.length; i++) {//for
            lis[i].index = i;//i
            lis[i].onclick = function () {//
                currentIndex = this.index
                carousel(currentIndex);
            }
        }
        function auto() {//
            id = setInterval(next, 4000)//
        }
        function stop() {
            clearInterval(id)
        }
        auto()
        document.querySelector('div').onmouseover = stop//div
        document.querySelector('div').onmouseout = auto//div
    },  
}
</script>
<style scoped>
    .banner{
        height: 720px;
        margin-top: -100px;
    }
    .banner-img{
        position: relative;
        height: 100%;
        width: 100%;
    }
    .carousel{
        position:absolute;
        width:100%;
        height:100%;
    }
      .carousel li{
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        width:100%;
        height:100%;
        transition: opacity .5s;
    }
    .carousel li:nth-child(1){
        background-repeat: no-repeat;
        background-image: url('https://ccdn.goodq.top/caches/9b69daf/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNS81OTE2NjgzNjA1NzgxOWEwNWM0ZmY2ZDk1YWMzNTQ4Yi05MC53ZWJw.webp');
        background-size: cover;
        background-position: center center;
    }
    .carousel li:nth-child(2){
        background-repeat: no-repeat;
        background-image: url('https://ccdn.goodq.top/caches/9b69daffc17579b848483c854813cad2/aHR0cDovLzVjZGQzYWE1YzQ2ZWEudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxOS8wNS82MWE3MmU5YWE1OGE3NWFmNjRkYWUyODNhMDMwNDU0MC05MC53ZWJw.webp');
        background-size: cover;
        background-position: center center;
    }
       .carousel .selection {
        opacity: 1;
        z-index: 10;
    }
    .circle {
        position: absolute;
        height: 8px;
        left: 50%;
        transform: translateX(-50%);
        z-index: 200;
        bottom: 20px;
    }
    .circle li {
        width: 60px;
        height: 5px;
        background: white;
        border: 1px solid transparent;
        float: left;
        margin-left: 10px;
        cursor: pointer;
    }
    .circle .current {
        background:#85bf41;
    }
</style>