let getters={
    
    go(state){
        state.nlist=[...state.newslist.splice(0,2)]
        console.log(state.nlist)
        return state.nlist
    },
    news(state){
        let arr = []
        for (let i = 0; i < state.newlist.length; i++) {
            if(state.newtsd === ''){
                arr.push(state.newlist[i])
            }else if(state.newtsd === state.newlist[i].type){
                arr.push(state.newlist[i])
            }
        }
        return arr
      },

      
    }
    
    let mutations = {
        
        m_arr(state,num){
          let type=true;
          for( let i=0;i<state.narr.length;i++){
              if(num.id==state.narr[i].id){
                  state.narr[i].s+=1
                  type=false
              }
          }
          if(type){
              state.narr.push(num)
            }
        },
       
}

let actions = {
    go1({commit}){
        commit('go')
    },
    brr({commit},num){
        commit('m_arr',num)
    }

}

export default new Vuex.Store({

    state,

    getters,

    mutations,

    actions,

})